import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;
const RadioGroup = Radio.Group;
import UploadImg from "./uploadImg.jsx"
import AdvContentSelect from "./advContentSelect.jsx"
export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            title: "",
            img: {
                file: {},
                fileList: []
            },
            time: "",
            status: "1",
            link: "",
            type: "1",
            advContent: {
                "type": "",
                "keyWord": "",
                "result": "",
                "selectList": []
            },
            loading: true
        };
        ['checkImg', 'handleSubmit', 'resetForm', 'showLoading', 'hideLoading'].forEach(m=>this[m] = this[m].bind(this))
    }

    showLoading() {
        this.setState({loading: true});
    }

    hideLoading() {
        this.setState({loading: false});
    }
    componentWillMount() {
        let that = this;
        if(this.props.type == "add") {
            this.setState({loading: false});
        } else {
            ajax({
                url: "/adv/getInfoDetail",
                data: {
                    id: this.props.advID
                },
                callback: function(data) {
                    if(data.status == 200) {
                        that.setState({
                            loading: false,
                            name: data.data.detailInfo.name,
                            img: {
                                file: {},
                                fileList: [{
                                    uid: -1,
                                    name: data.data.detailInfo.img.split("/").pop(),
                                    status: "done",
                                    url: data.data.detailInfo.img
                                }]
                            },
                            time: data.data.detailInfo.time,
                            status: data.data.detailInfo.status,
                            type: data.data.detailInfo.type,
                            link: data.data.detailInfo.link,
                            advContent: {
                                "type": data.data.detailInfo.advContentType,
                                "keyWord": data.data.detailInfo.advContentKeyWord,
                                "result": data.data.detailInfo.advContentResult
                            }
                        });
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            });
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(this.props.type == "edit") {
                    values = Object.assign(values, {id: this.props.noticeID});
                }
                values.img = values.img.fileList[0].response ? values.img.fileList[0].response.data.imgUrl : values.img.fileList[0].url;
                ajax({
                    url: "/adv/updateInfo",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("设置成功!", 1, function() {
                                hashHistory.push("/adv")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    returnList() {
        hashHistory.push("/adv");
    }

    resetForm() {
        this.setState({
            img: {
                fileList: []
            }
        });
        this.props.form.resetFields();
    }

    checkImg(rule, value, callback) {
        if(value.fileList.length > 0 && value.fileList[0].status == "done") {
            callback();
            return;
        }
        callback("请选择图片!");
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 10 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="广告名称"
                        hasFeedback
                    >
                        {getFieldDecorator('name', {
                            rules: [
                                { required: true, message: '请输入广告名称!' }
                            ],
                            initialValue: this.state.name
                        })(
                            <Input type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="广告类型"
                        hasFeedback
                    >
                        {getFieldDecorator('type', {
                            initialValue: this.state.type.toString()
                        })(
                            <RadioGroup>
                                <Radio value="1">桌面广告</Radio>
                                <Radio value="2">启动广告</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="图片"
                        hasFeedback
                    >
                        {getFieldDecorator('img', {
                            rules: [{
                                validator: this.checkImg
                            }],
                            initialValue: this.state.img
                        })(
                            <UploadImg showLoading={this.showLoading} hideLoading={this.hideLoading} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="是否显示"
                    >
                        {getFieldDecorator('status', {
                            initialValue: this.state.status.toString()
                        })(
                            <RadioGroup>
                                <Radio value="1">显示</Radio>
                                <Radio value="0">不显示</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="广告内容"
                    >
                        {getFieldDecorator('advContent', {
                            initialValue: this.state.advContent
                        })(
                            <AdvContentSelect showLoading={this.showLoading} hideLoading={this.hideLoading} />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        { !this.state.disabled ? <Button type="primary" htmlType="submit" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button> : ""}

                        <Button onClick={this.returnList} style={{"marginRight": "20px"}}>取消</Button>
                        { !this.state.disabled ? <Button type="danger" onClick={this.resetForm}>重置</Button> : ""}
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


