import React, {Component} from 'react'
import { Form, Input, Select, Button } from 'antd';
const Option = Select.Option;

class AdvContentSelect extends React.Component {
    constructor(props) {
        super(props);
        const value = this.props.value || {};
        this.state = {
            type: value.type || "1",
            keyWord: value.keyWord || '',
            result: value.result || "",
            selectList: []
        };
        this.typeList = [{
            "key": "1",
            "value": "网站委托项目"
        }, {
            "key": "2",
            "value": "用户发布项目"
        }, {
            "key": "3",
            "value": "网站委托资金"
        }, {
            "key": "4",
            "value": "用户发布资金"
        }, {
            "key": "5",
            "value": "网站活动"
        }, {
            "key": "6",
            "value": "网站公告"
        }];
        ['handleContentTypeChange', 'handleKeyWordChange', 'handleContentChange', 'loadSelectListData'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.loadSelectListData();
    }

    componentWillReceiveProps(nextProps) {
        if ('value' in nextProps) {
            const value = nextProps.value;
            this.setState(value);
        }
    }

    handleContentTypeChange(value) {
        const type = value;
        if (!('value' in this.props)) {
            this.setState({ type });
        }
        this.triggerChange({ type });
    }

    handleKeyWordChange(e) {
        const keyWord = e.target.value;
        if (!('value' in this.props)) {
            this.setState({ keyWord });
        }
        this.triggerChange({ keyWord });
    }

    handleContentChange(value) {
        const result = value;
        if (!('value' in this.props)) {
            this.setState({ result });
        }
        this.triggerChange({ result });
    }

    loadSelectListData() {
        let that = this;
        this.props.showLoading();
        ajax({
            url: "/adv/getContentList",
            data: {
                type: this.state.type,
                keyWord: this.state.keyWord
            },
            callback: function(data) {
                that.props.hideLoading();
                if(data.status == 200) {
                    that.setState({
                        selectList: data.data.list
                    })
                }
            }
        })
    }

    triggerChange(changedValue) {
        const onChange = this.props.onChange;
        if (onChange) {
            onChange(Object.assign({}, this.state, changedValue));
        }
    }

    render() {
        const state = this.state;
        return(
            <span>
                <Select
                    value={state.type || "1"}
                    onChange={this.handleContentTypeChange}
                    style={{width: '25%', marginRight: '2%'}}
                >
                    {this.typeList.map(item=>
                        <Option key={item.key} value={item.key}>{item.value}</Option>
                    )}
                </Select>
                <Input
                    type="text"
                    value={state.keyWord}
                    onChange={this.handleKeyWordChange}
                    style={{ width: '40%', marginRight: '2%' }}
                />
                <Button
                    type="primary"
                    onClick={this.loadSelectListData}
                >
                    搜索
                </Button>
                <Select
                    value={state.result}
                    onChange={this.handleContentChange}
                    style={{width: '67%', marginTop: 20}}
                >
                    <Option value="">请选择广告内容</Option>
                    {state.selectList.map(item=>
                        <Option key={item.key} value={item.key}>{item.value}</Option>
                    )}
                </Select>
            </span>
        )
    }
}

module.exports = AdvContentSelect;


